#section-two-features {
  max-width: $max-width;
  margin: 0 auto 160px auto;

  @media (max-width: 600px) {
    margin-bottom: 80px;
    max-width: 100%; }


  .title {
    font-family: $font-family-heading;
    font-style: normal;
    font-weight: bold;
    font-size: 42px;
    line-height: 44px;
    text-align: center;
    margin-bottom: 64px;
    color: #FFFFFF;
    padding: 0 20px;
    @media (max-width: 600px) {
      padding: 0 16px;
      font-size: 32px;
      line-height: 40px; } }

  .feature_title {
    margin-bottom: 5px; }

  .product_title_text {
    // width: 753px
    font-family: $font-family-body;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0px;
    text-align: center;
    color: rgba(161, 161, 161, 1);
    margin: 0 auto 60px;

    @media (max-width: 400px) {
      font-size: 14px;
      padding: 15px; } }

  .card-deck {
    display: flex;
    justify-content: center;
    margin: 0; }

  .col-12 {
    padding: 10px; }

  .card {
    margin: auto;
    background: linear-gradient(180deg, #2D2D2D 0%, #1C1C1C 100%);
    border-radius: 16px;
    border: 0;
    padding: 40px;
    align-items: center;
    min-height: 422px;
    @media (max-width: 600px) {
      padding: 40px 24px 32px 24px;
      border-radius: 12px;
      min-height: auto; }

    &:nth-of-type(1) {
      @media (max-width: 600px) {
        margin-bottom: 40px; } } }

  .img-container {
    height: 66.87px;
    margin-bottom: 30px;

    img {
      height: 100%; } }

  .img-container-head {
    height: 120px; }

  .card-body {
    @media (max-width: 600px) {
      padding: 0px; } }

  .card-title {
    font-family: $font-family-heading;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    margin-bottom: 16px;
    color: #FFFFFF;
    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 32px; } }

  .card-text {
    font-family: $font-family-body;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    color: rgba(255, 255, 255, 0.64); } }
