$font-family-heading: 'Syne', sans-serif;
$font-family-body: 'Inter', sans-serif;
$max-width: 1200px;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

// @media screen and (max-width: 209px)
//   .mahadao-logo, .two-buttons, .button1, .button2, .right
//     width: 100%

button {
  border: 0;
  outline: none; }

body {
  background-color: #161616;
  font-family: $font-family-body; }

html {
  display: block;
  height: 100%;
  width: 100%;
  overflow-x: hidden; }

body {
  display: block;
  min-height: 100%;
  width: 100%;
  background-color: #161616;
  position: relative; }

/* scrollbar */
::-webkit-scrollbar {
  width: 5px; }
::-webkit-scrollbar-track {
  background: rgba(#000, 0.1); }
::-webkit-scrollbar-thumb {
  background: #FF8B71; }
::-webkit-scrollbar-thumb:hover {
  background: #A81E41; }

