.custom-mahadao-box {
  position:relative;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(21px);
  padding: 32px;
  border-radius: 12px;
}

.custom-mahadao-box::before {
  content:"";
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  border-radius: 12px;
  padding:1px;
  background: linear-gradient(180deg, rgba(255, 116, 38, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
  -webkit-mask:
          linear-gradient(#fff 0 0) content-box,
          linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  z-index: -1;
}

@media (max-width: 600px){
  .custom-mahadao-box {
    padding: 32px 24px;
  }
}

.custom-mahadao-container {
  background: linear-gradient(180deg, #48423E 0%, #373030 100%);
