  0px,
  center center,
  100%,
  color-stop(0%, rgba(252, 252, 252, 0.21)),
  color-stop(1%, rgba(252, 252, 252, 0.21)),
  color-stop(4%, rgba(252, 252, 252, 0.22)),
  color-stop(27%, rgba(252, 252, 252, 0.31)),
  color-stop(57%, rgba(252, 252, 252, 0.43)),
  color-stop(100%, rgba(175, 175, 175, 0.61)));
  /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(center,
  ellipse cover,
  rgba(252, 252, 252, 0.21) 0%,
  rgba(252, 252, 252, 0.21) 1%,
  rgba(252, 252, 252, 0.22) 4%,
  rgba(252, 252, 252, 0.31) 27%,
  rgba(252, 252, 252, 0.43) 57%,
  rgba(175, 175, 175, 0.61) 100%);
  /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(center,
  ellipse cover,
  rgba(252, 252, 252, 0.21) 0%,
  rgba(252, 252, 252, 0.21) 1%,
  rgba(252, 252, 252, 0.22) 4%,
  rgba(252, 252, 252, 0.31) 27%,
  rgba(252, 252, 252, 0.43) 57%,
  rgba(175, 175, 175, 0.61) 100%);
  /* Opera 12+ */
  background: -ms-radial-gradient(center,
  ellipse cover,
  rgba(252, 252, 252, 0.21) 0%,
  rgba(252, 252, 252, 0.21) 1%,
  rgba(252, 252, 252, 0.22) 4%,
  rgba(252, 252, 252, 0.31) 27%,
  rgba(252, 252, 252, 0.43) 57%,
  rgba(175, 175, 175, 0.61) 100%);
  /* IE10+ */
  background: radial-gradient(ellipse at center,
  rgba(252, 252, 252, 0.21) 0%,
  rgba(252, 252, 252, 0.21) 1%,
  rgba(252, 252, 252, 0.22) 4%,
  rgba(252, 252, 252, 0.31) 27%,
  rgba(252, 252, 252, 0.43) 57%,
  rgba(175, 175, 175, 0.61) 100%);
